import React, { useState } from "react";
import { Button, Offcanvas } from "react-bootstrap";
import Comment from "./Comment";

export default function Content(props) {
  
    function randomContent() {
        let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz";
        let info = '';
        let length = (Math.floor(20 + Math.random() * 80));
        let count = 0;
        while (length !== 0) {
            if (count === 10) {
                info += '\n'
                count = 0;
            }
            let r = possible.charAt(Math.floor(Math.random() * possible.length));
            info += r;
            count++;
            length--;
        }
        return info
    }
    let infoARR = [];
    for (let i = 0; i < 60; i++) {
        infoARR[i] = randomContent();
    }
 
    function Example() {
        const [show, setShow] = useState(false);

        const handleClose = () => setShow(false);
        const handleShow =(e)=>{
            setShow(true);
            setObject(e.target.id);
            
        }
        const [comment, setComment] = useState([]);
        const [object,setObject]=useState();
       
        function addComment(id,text) {
            const newComment = { Sentence: id, comment: text };
            setComment([...comment, newComment]);
        }
        
  

        return (
            <>
                {
               
                    infoARR.map((item) => {
                        return (
                          
                            <>
                            <Button variant="light" id={item} onClick={handleShow}>{item}</Button>
                             
                                <Offcanvas show={show} onHide={handleClose} placement='end' backdrop={false}>
                                    <Offcanvas.Header closeButton>
                                        <Offcanvas.Title>Comments</Offcanvas.Title>
                                    </Offcanvas.Header>
                                    <Offcanvas.Body>
                                        <Comment key={object} addComment={addComment} name={object} text={comment}/>
                                        
                                     
                                    </Offcanvas.Body>
                                </Offcanvas>
                            </>
                        );
                    })
                }
            </>
        );

    }



    return (

        <div style={{
            border: 'solid', borderColor: "gray", display: "block", marginLeft: '80px', marginRight:
                '90px'
        }}>
            <h1 style={{ textAlign: 'center', fontSize: '40px' ,color:'gray'}}>Hello world</h1>
            <Example />

        </div>
    );
};