import React from "react";

import { StoreBook } from "./Book";
import { useEffect, useState } from 'react';
import axios from 'axios';
import { useStateStore } from "./Store";
function Bookshelf() {

  const state = useStateStore();
  const [imageState, setImageState] = useState({
    reading: false,
    image: null,
  });
  let num = Math.floor(Math.random() * 10);

  useEffect(() => {
    async function fetchImage() {
      setImageState({ reading: true });
      const apiUrl = `https://api.unsplash.com/photos?query=books&client_id=DzTfuBGG8HwVKm9fuxmhGGUblfPsoIv8PrYapsi4XKE`;
      const res = await axios.get(apiUrl);

      setImageState({ reading: false, image: res.data[num].urls.regular });
    }
    fetchImage();
  }, [setImageState])
  return (
    <div style={state.light ? { backgroundColor: '#212529' } : { backgroundColor: '#F8F9FA' }}>
      <StoreBook img={imageState.image}/>
    </div >
  );

}
export default Bookshelf