
import React, { useState } from 'react';
import { Card, Row, Col, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { nanoid } from 'nanoid';
import OneBook from './OneBook';

var A = [];

export default function SingleBook(props) {

  const { books } = props;
  function BookList() {
    if (props.isLoading) return (<p style={{ textAlign: 'center', fontSize: '30px', color: '#FF425E' }}>
      Hold on, Fetching data may take time
    </p>)
    if (!books || books.length === 0) return <p Style="color:#FF425E;">No books,Sorry</p>;
    return (
      <Row xs={2} md={3} lg={4} className='g-4' style={{ paddingTop: '20px' }}>

        {
          props.searchInputLength > 1 ?
            (props.filteredResults.map((item) => {
              return (<OneBook
                id={item.name }
                key={item.name+nanoid()}
                name={item.name}
                author={item.author}
                random={Math.floor(Math.random() * 9)}
                arr={A}
              />)
            }))
            : (books.map((item) => {
              return (<OneBook
                id={item.name }
                key={item.name+nanoid()}
                name={item.name} author={item.author}
                random={Math.floor(Math.random() * 9)}
                arr={A}
              />)
            }))
        }
      </Row>
    );
  }
  return (<BookList />)


};


export function StoreBook(props) {
  const [bookList, setBookList] = useState(A);

  function deleteBook(e) {

    const remainingBook = bookList.filter(book => e.target.id !== book.name);
    A = remainingBook;
    setBookList(remainingBook);

  }

  const storeTemplete = (


    bookList.map(books => {
      return (
        <Col key={books.name} >
          <Card className='card' style={{ width: '20rem' }}  >
            <Card.Img variant="top" src={props.img} width='155' height='207' />
            <Card.Body>
              <Card.Title>{books.name}</Card.Title>
              <Card.Text>
                Author:{books.author}
              </Card.Text>
            </Card.Body>

            <Card.Body>
              <Button variant="outline-primary" size='sm' type='submit' ><Link to="/content">Read</Link></Button>
              {' '}
              <Button id={books.name} onClick={deleteBook}
                variant="outline-danger"
                size='sm'>Remove</Button>
            </Card.Body>
          </Card>
        </Col>
      );
    })


  );
  
  if (!bookList || bookList.length === 0) return (<h1 style={{color:"#FF425E"}}>Go~Find your favorite books</h1>);
  return (
    <Row xs={2} md={3} lg={4} className='g-4' style={{ paddingTop: '20px' }}>
      {storeTemplete}
    </Row>

  );


}
