import Carousel from 'react-bootstrap/Carousel'
import log1 from '../images/logo.png';
import log2 from '../images/logo.jpeg';
import { useEffect, useState } from 'react';
import axios from 'axios';

export default function Adv(props) {
    const [imageState, setImageState] = useState({
        reading: false,
        image: null,
    });

    useEffect(() => {
        async function fetchImage() {
            setImageState({ reading: true });
            const apiUrl = `https://api.unsplash.com/photos?query=girls&client_id=DzTfuBGG8HwVKm9fuxmhGGUblfPsoIv8PrYapsi4XKE`;
            const res = await axios.get(apiUrl);

            setImageState({ reading: false, image: res.data[props.random].urls.full });
        }
        fetchImage();
    }, [setImageState])

   
    return (
        <>
            <Carousel pause='hover' variant="dark">
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={imageState.image}
                        alt="First slide"
                        style={{ maxHeight: "700px" }}
                    />
                    <Carousel.Caption>
                        <h3>First slide label</h3>
                        <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={log2}
                        alt="First slide"
                        style={{ maxHeight: "700px" }}
                    />
                    <Carousel.Caption>
                        <h3>2nd slide label</h3>
                        <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={log1}
                        alt="First slide"
                        style={{ maxHeight: "700px" }}
                    />
                    <Carousel.Caption>
                        <h3>3re slide label</h3>
                        <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </Carousel.Caption>
                </Carousel.Item>

            </Carousel>


        </>
    );
}