import React, { useEffect, useState } from 'react';
import { Row, Col, Button, Form, FormControl } from 'react-bootstrap';
import Adv from './Adverstment';
import SingleBook from './Book';
import axios from 'axios';

import { useStateStore } from './Store';

function App() {
  const state = useStateStore();
  const [searchInput, setSearchInput] = useState('');
  const [filteredResults, setFilteredResults] = useState([]);
  const [appState, setAppState] = useState({
    loading: false,
    books: null,

  });
  useEffect(() => {
    async function fetchData() {
      setAppState({ loading: true });
      const apiUrl = 'https://www.abibliadigital.com.br/api/books';
      const res = await axios.get(apiUrl);

      setAppState({ loading: false, books: res.data.slice(0, 20), completed: false })
    }
    fetchData();
  }, [setAppState])
  const searchBooks = (searchValue) => {
    setSearchInput(searchValue)
    if (searchInput !== " ") {
      const filteredData = appState.books.filter((item) => {
        return Object.values(item).join('').toLowerCase().includes(searchInput.toLowerCase())
      })
      setFilteredResults(filteredData);
    } else {
      setFilteredResults(appState.books);
    }

  }
  const searchBar = (<Form className="d-flex">
    <FormControl
      type="search"
      placeholder="Search Your Favorite books~"
      className="me-2"
      aria-label="Search"
      onChange={(e) => searchBooks(e.target.value)}
    />
  </Form>
  )










  return (
    <div style={state.light ? { backgroundColor: '#212529' } : { backgroundColor: '#F8F9FA' }}>
      {searchBar}
      {/* 可能行，不过要等到data fetch */}

      <Adv random={Math.floor(Math.random() * 8)} />

      <SingleBook
        isLoading={appState.loading}
        books={appState.books}
        searchInputLength={searchInput.length}
        filteredResults={filteredResults}
        key={"bookList"}
      />
    </div>

  )
}
export default App;