import React, { useEffect, useState } from 'react';
import { Card, Row, Col, Button } from 'react-bootstrap';
import axios from 'axios';
import { Link } from 'react-router-dom';
export default function OneBook(props) {
  const A = props.arr;
  const [isAdded, setAdded] = useState(false);
  const [imageState, setImageState] = useState({
    reading: false,
    image: null,
  });



  useEffect(() => {
    async function fetchImage() {

      setImageState({ reading: true });

      const apiUrl = `https://api.unsplash.com/photos?query=${props.name}&client_id=DzTfuBGG8HwVKm9fuxmhGGUblfPsoIv8PrYapsi4XKE`;
      const res = await axios.get(apiUrl);
      setImageState({ reading: false, image: res.data[props.random].urls.small });

    }
    fetchImage();
  }, [setImageState])
  function handleChange(e) {
    if (e.target.id === props.id) {
      setAdded(!isAdded);
      let check = false;
      if (A.length > 0) {

        for (let i = 0; i < A.length; i++) {
          if (A[i] === props) {
            check = true;
            A.splice(i, 1);
            break;
          }
        }
      }
      if (check === false) {
        A.push(props);
      }
    }
  }
  

  return (
    <Col  >

      <Card id={props.id} name={props.name} className='card box-shadow' style={{ width: '20rem' }} text="muted" >
        <div className='card-header'>
          <Card.Img variant="top" src={imageState.image} width='155' height='207' />
          </div>
        <Card.Body className="card-body">
          <Card.Title >{props.name}</Card.Title>
          <Card.Text >
            Author:{props.author}
          </Card.Text>
        </Card.Body>
       

        <Card.Body className="card-footer">
          <Button variant="outline-primary" size='sm' type='submit'>
            <Link to="/content">
              Read
            </Link>
          </Button>
          {' '}
          <Button id={props.id} variant={isAdded ? "warning" : "outline-warning"} onClick={handleChange} size='sm'>add to book shelf</Button>

        </Card.Body>
      </Card>
    </Col>



  );
}

