import React from "react";
import Modal from 'react-bootstrap/Modal'
import Button from "react-bootstrap/Button";

export default function Doc(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Hello~ World
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <h2>Introduction</h2>
        <p>As you can see, this is a program for reading web pages. How to use it? It's simple.
          The first thing you see is the main page of this program, which contains a navigation bar with 
          Logo, the name of this website, the Home, bookShelf and a SWITCH that can change the 
          reading model of the website.</p>
          <p>then the Search bar.</p>
          <p>The section under the search  bar that occupies a large area is the announcement. 
            There may be something you are interested in here? Maybe~~haha</p>
            <p>Then there is the area that enriches your soul ~ ~ see the blue and yellow buttons? If you want to read, please click the blue button. if you like this book very much, 
              please click the yellow button to collect it in your BOOK SHELF.</p>
        <h3>Search Bar</h3>
        <p>How to find a book? </p>
        <p>All you have to do is type the title of the book you want to read into the search box. It's simple, right?</p>
        <h3>Home</h3>
        <p>Wanna go back to home page? </p>
        <p>Click the Home key,or the logo on your left up corner</p>
        <h3>Book Shelf</h3>
        <p>What is the use of it? </p>
        <p>Keep your favorite books in it so that you can read them later.</p>
      
        <h3>Book Sources</h3>
        <p>
          All Books are from <a href='httas://www.abibliadigital.com.br/api/books'>A bibli Digital</a>
        </p>
        <p>Hope you can figure out what are the contents about~~</p>
        <h3>Code Sources</h3>
        <p>This project i built it with React-Boostrap <strong>Thanks for Adnan i don't need to type a lot!!!!Orz</strong> </p>
        
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}
