import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Navbar, Nav, Container, Form } from 'react-bootstrap';
import Button from "@restart/ui/esm/Button";
import Doc from './Doc';
import logo2 from '../images/DAY.png'
import logo from'../images/DARK.png'
import { useDispatchStore, useStateStore } from "./Store";



function NavBar(props) {
  const [modalShow, setModalShow] = useState(false);
  const state = useStateStore();
  const dispatch = useDispatchStore()
  useEffect(() => {
    const newTheme = localStorage.getItem('theme');
   if(newTheme){
     const check = JSON.parse(newTheme);
     if(check===true){
       dispatch({type:'dark'});
     }else{
      dispatch({type:'light'});
     }
   }
  }, [])
  useEffect(()=>{
    localStorage.setItem('theme', JSON.stringify(state.light))
  })
  

  const navigation = (
    <Container>
      <Navbar.Brand >
        <Link to="/">
        <img
          alt=""
          src={state.light?logo:logo2}
          width="100"
          height="50"
          className="d-inline-block align-top"
        /></Link>
      </Navbar.Brand>
      <Navbar.Collapse id="navbarScroll">
        <Nav
          className="me-auto my-2 my-lg-0"
          style={{ maxHeight: '100px' }}
          navbarScroll
        >
          <Nav.Link ><Link to="/">Home</Link></Nav.Link>
          <Nav.Link><Link to="/Bookshelf">Book Shelf</Link></Nav.Link>
         

          <Form >
            <Form.Check
              type="switch"
              id="custom-switch"
              onClick={() => state.light === false ? dispatch({ type: "dark" }) : dispatch({ type: "light" })}

            />
          </Form>

        </Nav>
      </Navbar.Collapse>

    </Container>
  );


  return (

    <Navbar variant={state.light?'dark':'light'} bg={state.light?'dark':'light'} expand="lg" >
      {navigation}
      <Button variant="primary" onClick={() => setModalShow(true)}>
        Documentaion
      </Button>

      <Doc
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </Navbar>

  );
}
export default NavBar